.row {
  display: flex;
  flex-direction: row;
}

.col {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.form-login {
  background-color: #4e7887;
  border: solid 5px #c0ced2;
  border-radius: 50px;
  color: #c0ced2;
  margin: 200px auto;
  padding: 70px 150px 100px;
  width: 400px;
}

.form-login input {
  border-radius: 8px;
  font-size: 18px;
  margin-bottom: 30px;
  padding: 12px;
  width: 80%;
}

.form-login label {
  width: 80%;
}

.form-login button {
  border-radius: 8px;
  font-size: 18px;
  padding: 10px;
  width: 87%;
}

.container-header {
  align-items: center;
  background-color: #123c4d;
  color: white;
  padding: 10px 40px;
}

.header-logo {
  width: 30px;
}

.container-header h1 {
  width: 400px;
}

.header-data {
  align-items: center;
  justify-content: flex-end;
  width: 100%;
}

.header-data p {
  font-size: 18px;
  font-weight: 500;
  padding: 0 30px;
}


.header-data span {
  font-size: 18px;
  font-style: italic;
  font-weight: 300;
  padding-left: 15px;
  width: 150px;
}

.container-expense {
  background-color: #4e7887;
  font-size: 18px;
  height: 50px;
  justify-content: center;
  padding: 20px 10px;
}

.form-expense {
  justify-content: space-between;
}

.form-expense label {
  color: #c0ced2;
}

.form-expense input , select {
  border: none;
  border-radius: 5px;
  margin-left: 5px;
  margin-right: 30px;
  padding: 5px;
}

.form-expense button {
  border: none;
  border-radius: 5px;
  padding: 0 20px;
}

.form-expense button:hover {
  background-color: #123c4d;
  color: #c0ced2;
  cursor: pointer;
}

*[ id = 'value' ] {
  width: 80px;
}

*[ id = 'description' ] {
  width: 200px;
}

*[ id = 'currency' ] {
  width: 60px;
}


*[ id = 'method' ] {
  width: 140px;
}

.table-container {
  justify-content: center;
  width: 100%;
}

.table.container-th {
  justify-content: space-between;
  width: 100%;
}

.table.container-th th {
  width: 10%;
}
